import React,{useEffect} from "react";
import "./ContactPage.css";
import Iframe from 'react-iframe';
import { Link } from "react-router-dom";
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const ContactPage = () => {
    const navigate = useNavigate();
    const handleClick = () => {
        navigate('/');
      };

      useEffect(() => {
        window.scrollTo(0, 0);
      }, []);


    return (
        <div className="contactpage-section">
            <div className="contactpage">
                <div className="form">
                    <div className="back" onClick={handleClick}><h2>Back</h2></div>
                    <div className="form-container">
                        <h5 className="h5">Contact</h5>
                        {/* <h1 className="h1">r.pravinkanth653@gmail.com</h1> */}
                        <p className="p">Like what you see? Have something youd like to talk about?<br></br>
                        Want to ask me a personal question?</p>

                        <form onSubmit="submit" className="theform" name="Portfolio" method="post" data-netlify="true">
                            <input type="hidden" name="form-name" value="Portfolio"/>
                            <input className="fullname" name="Fullname" placeholder="FULL NAME" type="text" required></input>
                            <input className="fullname" name="Email" placeholder="EMAIL ADDRESS" type="email" required></input>
                            <textarea className="fullname textarea" name="Message" rows="7" placeholder="ASK ME SOMETHING"></textarea>
                            <button type="submit" className="button">send</button>
                        </form>
                        
                    </div>
                </div>
                    <div className="map">
                        <Iframe 
                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d687029.3923413393!2d76.9825756567899!3d11.158213148190631!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3ba8ffed1a9e40bd%3A0x11237dd63b745a5d!2sPravin%20Kanth%20tex!5e0!3m2!1sen!2sin!4v1686334379155!5m2!1sen!2sin"
                        width="600"
                        className="frame"
                        
                        height="750" 
                        style="border:0;" 
                        allowfullscreen="" 
                        loading="lazy" 
                        referrerpolicy="no-referrer-when-downgrade">
                        </Iframe>      
                    </div>
            </div>
  </div>
    );
};

export default ContactPage;