import React,{useState} from "react";
import "./Pursuits.css";
import skull from "../../assets/skull.svg";
import ag from "../../assets/ag.svg";


const Pursuits = () =>{
        
        const [selectedDiv,setSelectedDiv]=useState(0);
        const handleSelectDiv = (sum) => {
            if (sum > 0) {
              setSelectedDiv((selectedDiv + sum) % 5);
            } else {
              let newSelectedDiv = selectedDiv - Math.abs(sum) % 5;
              if (newSelectedDiv < 0) {
                newSelectedDiv = 5 + newSelectedDiv;
              }
              setSelectedDiv(newSelectedDiv);
            }
          };

    return(
        <div className="cover">
        <div className="pursuits-section">
            <div className="pursuits">
                <div className="container">
                    <h5 className="h5">Things I do</h5>
                    <h2 className="lh2"><strong>Building bridges to </strong>Success</h2>
                    <div className="services">
                    <img decoding="async" src={ag} alt="Awesome guys bubble" class="tiny" data-rellax-speed="1" />
            <img decoding="async" src={skull} alt="Skull Icon" class="skull" data-rellax-speed="1"></img>
                    {/* <img decoding="async" data-aos="fade-up" data-aos-offset="50" src="https://squareme.si/wp-content/uploads/2019/10/heart-flagpole.svg" alt="Hreat flagpole illustration" class="flag-icon" data-rellax-speed="1" ></img> */}
                    

                        
                    
                   
                    <div  className="out-content">
                    <a onClick={()=> handleSelectDiv(-1)} className="left-arrow"><ion-icon name="chevron-back-outline"></ion-icon></a>
                    <a onClick={()=> handleSelectDiv(1)} className="right-arrow"><ion-icon name="chevron-forward-outline"></ion-icon></a>
                    {selectedDiv === 0 && <div className="content">
                        <h2 id="pursuits" className="h2">Full stack development</h2>
                        <p>Experienced full stack developer with a strong track record. Seamlessly integrates design and functionality to deliver impressive projects and exceptional user experiences.</p>
                        <ul className="trading-list">
                            <li>Front-End  : HTML5, CSS3, SASS, Javascript, ReactJS </li>
                            <li>Back-End : Django</li>
                            <li>Database : Postgres</li>
                            <li>Source Control : Git</li>
                        </ul>
                    </div>}

                    {selectedDiv === 1 && (
                  <div className="content">
                    <h2 id="pursuits" className="h2">Competitive Programming</h2>
                    <p>
                      Proficient in competitive programming with expertise in
                      solving algorithmic problems and participating in coding
                      competitions. Skilled in data structures, algorithms, and
                      problem-solving techniques to develop efficient and
                      optimized code solutions.
                    </p>
                    <ul className="trading-list">
                            <li>Languages: Python, Java</li>
                        </ul>
                  </div>
                )}

                    {selectedDiv === 2 && <div className="content">
                        <h2 id="pursuits" className="h2">UI / UX Designing</h2>
                        <p>Creative UI/UX designer with a flair for captivating experiences. Award-winning designer recognized for delivering innovative and visually stunning designs. Passionate about crafting intuitive user interfaces that engage and delight users.</p>
                        <ul className="trading-list">
                            <li>Wireframing and Prototyping</li>
                            <li>Visual Design</li>
                            <li>Tools: Figma, Webflow</li>

                        </ul>
                    </div>}

                    {selectedDiv === 3 && <div className="content">
                        <h2 id="pursuits" className="h2">Trading</h2>
                        <p>Experienced trader with a proven track record over 2 years. Expertise in analyzing market trends, executing strategic trades, and maximizing returns. Dedicated to continuous learning and staying ahead in the dynamic world of trading.</p>
                        <ul className="trading-list">
                            <li>Expert in harnessing Price Action power</li>
                            <li>Mastered the art of japanese candlestick and chart patterns</li>
                            <li>Proficient in comprehending market structure</li>
                            <li>Skilled in interpreting technical indicators(Moving averages, RSI etc.)</li>
                        </ul>
                    </div>}

                    {selectedDiv === 4 && <div className="content">
                        <h2 id="pursuits" className="h2">Investment</h2>
                        <p>Experienced investor with a proven history of successful ventures. Actively involved in supporting the growth and development of 5-6 dynamic companies. Committed to strategic investments that fuel innovation and deliver impressive returns.</p>
                        <ul className="trading-list">
                            <li>Expert in identifying long-term growth through fundamental analysis.</li>
                            <li>Highly skilled in technical analysis.</li>
                            <li>Honed my skills in effective pursuits diversification.</li>
                            <li>Skilled in interpreting technical indicators(Moving averages, RSI etc.)</li>
                        </ul>
                    </div>}

                    

                    </div>

                    <div className="down">
                        <div className="down-container">
                            <div className="dedication">
                                <span className="number">01</span>
                                <h3 className="title">Dedication</h3>
                                <div className="line"></div>
                                <p className="para">With unwavering dedication, I pursue my goals relentlessly, fueled by passion and perseverance.</p>
                            </div>
                            <div className="hardwork"> 
                                <span className="number">02</span>
                                <h3 className="title">Hardwork</h3>
                                <div className="line"></div>
                                <p className="para">Through unwavering determination, I am a standout performer in the world of hard work.</p></div>
                            <div className="intelligence">
                            <span className="number">03</span>
                                <h3 className="title">Intelligence</h3>
                                <div className="line"></div>
                                <p className="para">I am a cognitive powerhouse, constantly seeking new insights and pushing the boundaries of knowledge.</p>
                            </div>
                        </div>
                    </div>
                    </div>
                    
                    </div>


                    <a href="#portfolio" class="r90 left hide-el bl has-rotate hide-m" data-cursor="false" data-cursor-type="link">
                    <span class="text"> Portfolio&nbsp;</span>
    <span class="left"> &rarr;</span>
</a>
                

            </div>
        </div>
        </div>
    );
};

export default Pursuits;