import React from "react";
import "./About.css";
import cut from "../../assets/cut-text_09678c85.svg";
import box from '../../assets/box-cut.svg';
import flag from "../../assets/flag.svg";
import skull from "../../assets/skull.svg";
import ag from "../../assets/ag.svg";


const About = () =>{
    return(
        <div id="skills" className="section-skills">
            <div className="skills">
            <a href="#pursuits" class="r90 left hide-el bl has-rotate hide-m" data-cursor="false" data-cursor-type="link">
  
    <span class="text"> Pursuits&nbsp;</span>
    <span class="left"> &rarr;</span>
</a>
            <img decoding="async" src={ag} alt="Awesome guys bubble" class="tiny" data-rellax-speed="1" />
            <img decoding="async" src={skull} alt="Skull Icon" class="skull" data-rellax-speed="1"></img>


                <div id="skills-container"className="skills-container">

                    <div id="skills-left" className="skills-left">

                        <h5 id="h5" className="h5">About Me</h5>
                        <h2 class="lh2"><strong>Transforming <span className="span-parent">ideas<img className="cut" alt="cut" src={cut}/><span className="span-child">Inspirations</span></span> into Cutting-Edge Solutions with</strong> Passion<strong> and </strong>Precision<strong>.</strong>
</h2>

                        
                    </div>
                    <div className="skills-right">
                    <img class="jj" decoding="async" data-aos="fade-up" data-aos-offset="50" src={box} alt="Crossed out box illustration"  data-rellax-speed="-1" ></img>

                        <p className="lp">
                    Hey you! Welcome to my professional portfolio! I'm Pravin Kanth, a dedicated Computer Science student passionate about creating exceptional web experiences. With expertise in various web technologies, I specialize in crafting polished and engaging designs that leave a lasting impact. I consistently strive for excellence and have honed my skills in various fields to deliver professional and impactful solutions. My work speaks for itself, so take a look at my impressive portfolio below!

                    </p></div>

                </div>
                <img decoding="async" data-aos="fade-up" data-aos-offset="50" src={flag} alt="Hreat flagpole illustration" class="flag-icon" data-rellax-speed="1" ></img>
                <div className="change"></div>
            </div>
        </div>


    );
};

export default About;